/* Chat Container */
.chat-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  background-color: #ffffff;
}

/* Header */
.chat-header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 40px; /* Added left and right margins */
  background-color: #ffffff;
  border-bottom: none; /* Removed border under header */
}

/* Title */
.chat-title {
  font-size: 24px;
  font-weight: bold;
}

.sub-title {
  font-size: 14px;
  margin-top: 4px;
  display: block;
}

.header-link {
  text-decoration: none;
  color: black;
  font-weight: normal;
}

/* Sign In Button */
.sign-in-button {
  background-color: black;
  color: white;
  border: none;
  padding: 8px 14px;
  font-size: 14px;
  border-radius: 20px;
  cursor: pointer;
}

.sign-in-button:hover {
  opacity: 0.8;
}

/* Profile Menu */
.profile-menu {
  position: relative;
  display: inline-block;
}

.profile-icon {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  cursor: pointer;
}

.dropdown {
  display: none;
  position: absolute;
  right: 0;
  background-color: white;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  z-index: 1;
}

.profile-menu:hover .dropdown {
  display: block;
}

.dropdown button {
  width: 100%;
  padding: 8px;
  border: none;
  background: none;
  cursor: pointer;
}

/* Chat Messages */
.chat-messages {
  flex: 1;
  width: 100%;
  max-width: 600px;
  padding: 20px;
  overflow-y: hidden; /* Removed scroll bar */
  display: flex;
  flex-direction: column;
}

/* Chat Input */
.chat-input-container {
  width: 100%;
  max-width: 600px;
  padding: 10px;
  border-top: none; /* Removed border under header */
}

.chat-input-wrapper {
  display: flex;
  align-items: center;
  background: white;
  border-radius: 25px;
  border: 1px solid #ddd;
  padding: 8px;
}

.chat-input {
  flex: 1;
  border: none;
  padding: 10px;
  font-size: 16px;
  outline: none;
}

.send-button {
  background-color: black;
  color: white;
  border: none;
  padding: 8px 14px;
  font-size: 14px;
  border-radius: 20px;
  cursor: pointer;
}

.send-button:hover {
  opacity: 0.8;
}

/* Login Warning */
.login-warning {
  text-align: center;
  color: red;
  font-size: 14px;
  margin-top: 5px;
}
